}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.chat-window {
  width: 100%;
  height: 100%;
  background-color: #19222d;
  display: flex;
  flex-direction: column;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
}
.chat-content {
  flex-grow: 1;
  overflow-y: scroll;
}
.chat-content::-webkit-scrollbar {
  width: 5px;
}
.chat-content::-webkit-scrollbar {
  background-color: #2a3e53;
}
.custom-checkbox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.custom-checkbox input {
  display: none;
}
.checkmark {
  width: 32px;
  height: 20px;
  background: #2a3e53;
  border-radius: 20px;
  position: relative;
  transition: background 0.3s ease;
}
.checkmark:before {
  content: '';
  display: block;
  position: absolute;
  left: 1px;
  top: 1px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #19222d;
  transition: left 0.3s ease;
}
.custom-checkbox input:checked ~ .checkmark {
  background: #2ea6ff;
}
.custom-checkbox input:checked ~ .checkmark:before {
  left: 12px;
}
.nice-scroll::-webkit-scrollbar {
  width: 5px;
}
.nice-scroll::-webkit-scrollbar-thumb {
  background-color: #2a3e53;
}
.nice-scroll::-webkit-scrollbar-track {
  background: #19222d;
}
.select-window {
  font-size: 15px;
}
.select-window ul {
  width: 100%;
  display: flex;
}
.select-window a {
  color: #fff;
}
html,
body {
  height: 100%;
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box;
  font-size: 10px;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
.page__wrapper {
  min-height: 100%;
  box-sizing: inherit;
}
* html .page__wrapper {
  height: 100%;
}
.page__buffer {
  height: 50px;
}
.chat-content {
  padding: 20px;
  display: flex;
}
.chat-empty__ico {
  margin-bottom: 20px;
}
.chat-empty {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.chat-empty__text {
  max-width: 300px;
  text-align: center;
  color: #b1c3d5;
}
.chat-item {
  display: flex;
  max-width: 1000px;
  margin-bottom: 25px;
}
.chat-item:last-child {
  margin-bottom: 0;
}
.chat-item__img-cont {
  display: flex;
}
.chat-item__img {
  width: 38px;
  height: 38px;
  border-radius: 50%;
}
.chat-item__msg {
  margin-left: 15px;
}
.chat-item__name {
  color: #fff;
  text-decoration: none;
  margin-bottom: 10px;
  font-weight: 600;
}
.chat-item__text {
  font-weight: 400;
}
.chat-header {
  min-height: 50px;
  border-bottom: 1px solid #2a3e53;
  display: flex;
}
.chat-header__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.8rem 2rem;
  flex-grow: 1;
}
.chat-header__settings {
  position: relative;
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat-header__users {
  font-size: 12px;
  font-weight: 400;
  color: #b1c3d5;
}
.hidden {
  display: none;
}
.hidden:checked ~ .chat-settings-container {
  opacity: 1;
  pointer-events: all;
}
.chat-settings-btn .open {
  display: block;
}
.chat-settings-btn .close {
  display: none;
}
.hidden:checked ~ .chat-settings-btn .open {
  display: none;
}
.hidden:checked ~ .chat-settings-btn .close {
  display: block;
}
.chat-settings-btn {
  cursor: pointer;
}
.settings-item {
  margin-bottom: 20px;
  cursor: pointer;
}
.settings-item:last-child {
  margin-bottom: 0;
}
.chat-settings-container {
  transition: opacity 0.3s ease;
  position: absolute;
  right: 15px;
  top: 65px;
  opacity: 0;
  height: auto;
  pointer-events: none;
  width: 180px;
  padding: 10px;
  background-color: #19222d;
  box-shadow: 1px 1px 10px 0px rgba(0,0,0,0.35);
  border-radius: 8px;
}
.chat-message {
  display: flex;
  min-height: 50px;
  max-height: 164px;
  border-top: 1px solid #2a3e53;
  flex-shrink: 0;
  position: relative;
  padding: 15px;
}
.chat-editor {
  flex-grow: 1;
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
  outline: none;
}
.chat-editor:empty:before {
  content: attr(placeholder);
  color: #b1c3d5;
}
.chat-editor__container {
  width: calc(100% - 50px);
  overflow-y: scroll;
  padding-right: 10px;
}
.chat-send-btn {
  position: absolute;
  right: 10px;
  bottom: 9px;
  background: transparent;
  border: none;
  outline: none;
}
.chat-send-btn svg {
  transition: fill 0.3s ease;
  fill: #b1c3d5;
}
.chat-message:focus-within .chat-send-btn svg {
  fill: #4a90e2;
}
